.tb_c_container {
       padding: 15px;
       border-bottom: 1px solid rgba(184, 184, 184, .40);
       margin-bottom: 15px;
}

.tb_c_wrapper {
       width: 100%;
}

.tb_c_title {
       font-size: var(--tb-re-title-size);
       font-weight: 400;
       margin-bottom: 5px;
       text-align: center;
       color:var(--tb-re-title-color);
       font-family: var(--tb-re-title-font);
}

.tb_c_avg,
.tb_c_wrapper {
       display: flex;
       flex-wrap: wrap;
       align-items: center;
}

.tb_c_avg {
       margin-bottom: 10px;
}

.tb_c_wrapper {
       justify-content: space-between;
}

.tb_c_rating_wrap {
       display: flex;
       flex-direction: column;
       margin: 10px 0;
       min-width: 230px;
}

.tb_c_total,
.tb_c_rate_count,.tb_c_review_total_post {
       font-family: var(--tb-font);
       color: var(--tb-color);
       font-size: var(--tb-font-size);
       line-height: var(--tb-line-height);
}

.tb_c_rview_star {
       margin-right: 10px;
       min-width: 120px;
}

.tb_c_rview_star .tb_avg_rating_ico__,.tb_c_rview_star .tb_avg_rating_ico_fill__,.tb_c_avg_list .tb_avg_rating_ico__ {
       color: var(--tb-review-start-color);
}

.tb_c_banner_btn {
       background-color: var(--tb-btn-review-bg-color);
       color: var(--tb-btn-review-color);
       padding: 10px 20px;
       font-size: var(--tb-btn-review-size);
       transition: opacity .3s ease-in-out;
       cursor: pointer;
       font-family: var(--tb-btn-review-font);
}

.tb_c_banner_btn:hover {
       opacity: .8;
}

.tb_c_banner_btn_wrap {
       position: relative;
       margin: 8px 0;
}

.tb_c_avg_st {
       max-width: 300px;
       width: 100%;
}

.tb_c_avg_list {
       display: flex;
       align-items: center;
       margin: 10px 0px;
}

.tb_c_bar {
       width: 100%;
       background-color: #f4f4f4;
       height: 14px;
       margin: 0 10px;
       position: relative;
       overflow: hidden;
}

.tb_c_bar_active {
       height: 100%;
       position: absolute;
       // width: 90%;
       left: 0;
       top: 0;
       background-color: var(--tb-review-start-color)!important;
}


.tb_c_bar_active {
       animation: tb_animateBar 1s ease-in-out forwards;
}

@keyframes tb_animateBar {
       0% {
              transform: translateX(-100%);
       }

       100% {
              transform: translateX(0);
       }
}

@media screen and (max-width: 767px) {
       .tb_c_banner_btn {
              padding: 9px 14px;
                font-size: var(--tb-btn-review-size);
       }

       .tb_c_rview_star {
              margin-right: 5px;
       }

       .tb_c_rview_star .tb_avg_rating_ico__ {
              font-size: 14px;
              margin: 1px;
       }

       .tb_c_review_total_post,
       .tb_c_total {
              font-size: 12px;
       }
}

@media screen and (max-width: 480px) {
       .tb_c_banner_wrap {
              padding: 10px 15px;
       }
}